<template>
  <div id="page-registration">
    <Icon
      id="page-registration-btn-back"
      name="arrow-left"
      @click="jumpToPreviousPage"
    />
    <VanImage
      id="page-registration-icon"
      round
      width="144px"
      height="144px"
      fit="cover"
      :src="require('@/assets/others/default_avatar.png')"
      @click="jumpToPageHome"
    />
    <Form
      id="page-registration-form"
      ref="pageRegistrationForm"
      :show-error="false"
      @submit="doSubmitFormValues($event, urls.getRSAPubKey, urls.postRegiVal)"
    >
      <Field
        ref="registrationFieldUserName"
        v-model="formValues.userName"
        name="userName"
        label="用户名"
        placeholder="请输入6至20个字符（字母或数字）"
        :rules="rulesUserName"
      />
      <Field
        ref="registrationFieldPassword"
        v-model="formValues.password"
        name="password"
        type="password"
        label="密码"
        placeholder="请输入8至20个字符（字母或数字）"
        :rules="rulesPassword"
      />
      <Field
        ref="registrationFieldConfirmation"
        v-model="formValues.confirmation"
        name="confirmation"
        type="password"
        label="密码确认"
        placeholder="请再次输入密码"
        :rules="rulesConfirmation"
      />
      <div id="page-registration-btns">
        <Button
          id="page-registration-btn-submit"
          round
          block
          type="info"
          native-type="submit"
        >
          注册
        </Button>
        <Button
          id="page-registration-btn-login"
          round
          block
          type="default"
          native-type="button"
          @click="jumpToPageLogin"
        >
          已有账号？点我登录
        </Button>
      </div>
    </Form>
  </div>
</template>

<style scoped>
#page-registration {
  overflow: auto;
}

#page-registration-btn-back {
  position: absolute;
  top: 16px;
  left: 16px;
  width: 40px;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  color: white;
  background-color: var(--primary-color);
  border-radius: 50%;
}

#page-registration-icon {
  display: block;
  margin: 64px auto;
}

#page-registration-btns {
  padding: 32px 16px;
}

#page-registration-btn-login {
  margin-top: 16px;
}
</style>

<script>
import { SHA256Calculator } from '@/utils/hashCalculator.js'
import { axiosRenewAccessToken, axiosEncryptWithRSAOAEP } from '@/utils/axiosInterceptor.js'
import { urlAPIGetRSACryptionKey, urlAPIPostUserRegistration } from '@/api/requestURLs.js'
import { Button, Field, Form, Icon, Image as VanImage, Notify } from 'vant'

export default {
  name: 'PageRegistration',
  components: {
    Button,
    Field,
    Form,
    Icon,
    VanImage
  },
  data () {
    return {
      showBtnBack: true,
      urls: {
        getRSAPubKey: urlAPIGetRSACryptionKey,
        postRegiVal: urlAPIPostUserRegistration
      },
      routePath: {
        pathFrom: '',
        pathTo: ''
      },
      formValues: {
        userName: '',
        password: '',
        confirmation: ''
      },
      rulesUserName: [
        {
          required: true,
          message: '请填写用户名'
        },
        {
          message: '',
          validator: this.checkUserNameFormat
        }
      ],
      rulesPassword: [
        {
          required: true,
          message: '请填写密码'
        },
        {
          message: '',
          validator: this.checkPasswordFormat
        }
      ],
      rulesConfirmation: [
        {
          required: true,
          message: '请再次填写密码'
        },
        {
          message: '两次填写的密码不一致',
          validator: this.confirmPassword
        }
      ]
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.routePath = {
        pathFrom: from.path,
        pathTo: to.path
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    this.$refs.pageRegistrationForm.resetValidation()
    next()
  },
  methods: {
    // 检查用户名格式是否正确
    checkUserNameFormat (value, rule) {
      const pattern = /^[a-z0-9]+$/i
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '不应包含除字母和数字以外的字符'
      } else if (value.length < 6) {
        rule.message = '字符不应少于6个'
      } else if (value.length > 20) {
        rule.message = '字符不应多于20个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 检查密码格式是否正确
    checkPasswordFormat (value, rule) {
      const pattern = /^[a-z0-9]+$/i
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '不应包含除字母和数字以外的字符'
      } else if (value.length < 8) {
        rule.message = '字符不应少于8个'
      } else if (value.length > 20) {
        rule.message = '字符不应多于20个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 检查两次填写的密码是否相同
    confirmPassword (value) {
      return this.formValues.password === value
    },
    // 显示服务器连接失败通知
    notifyBadConnection () {
      Notify({
        type: 'danger',
        message: '连接服务器失败'
      })
    },
    // 从后端获取RSA-OAEP公钥
    getRSAPubKey (url) {
      return axiosRenewAccessToken().get(url)
        .then(response => response.data.payload.key)
        .catch(error => Promise.reject(error))
    },
    // 前端向后端发送加密后的信息
    postRSAEncryptedMsg (url, rsaPubKey, message) {
      return axiosEncryptWithRSAOAEP(rsaPubKey).post(url, message)
        .then(response => response)
        .catch(error => {
          const resMsg = error?.response?.data?.message
          if (resMsg?.includes('key: \'RequestUserLogin.userName\'')) {
            this.$refs.registrationFieldUserName.validateMessage = '用户名格式不正确'
          }
          if (resMsg?.includes('key: \'RequestUserLogin.password\'')) {
            this.$refs.registrationFieldPassword.validateMessage = '密码格式不正确'
          }
          if (resMsg === 'user already exists') {
            this.$refs.registrationFieldUserName.validateMessage = '用户名已存在'
          }
          return Promise.reject(error)
        })
    },
    // 异步提交表单数据
    async submitFormValues (values, urlGetRSAPubKey, urlpostRegiVal) {
      // 从后端获取RSA-OAEP公钥
      const rsaPubKey = await this.getRSAPubKey(urlGetRSAPubKey)
      // 加密相关表单数据
      const message = {
        userName: values.userName,
        password: SHA256Calculator(values.password)
      }
      // 前端向后端发送加密后的信息
      const response = await this.postRSAEncryptedMsg(urlpostRegiVal, rsaPubKey, message)
      // 返回响应结果
      return response
    },
    // 执行异步提交表单数据
    doSubmitFormValues (values, urlGetRSAPubKey, urlpostRegiVal) {
      // 未成功连接服务器前不执行后续操作
      if (!this.$store.getters.isConnected) {
        this.notifyBadConnection()
        return
      }
      // 在连接服务器的前提下，提交表单数据
      return this.submitFormValues(values, urlGetRSAPubKey, urlpostRegiVal)
        .then(response => {
          // 注册成功的通知
          Notify({
            type: 'success',
            message: '注册成功'
          })
        })
        .catch(() => {
          // 连接服务器失败
          if (!this.$store.getters.isConnected) {
            this.notifyBadConnection()
            return
          }
          // 注册失败
          Notify({
            type: 'danger',
            message: '注册失败'
          })
        })
    },
    // 跳转到前一页面
    jumpToPreviousPage () {
      this.$router.back()
    },
    // 跳转到首页
    jumpToPageHome () {
      this.$router.push('/')
    },
    // 跳转到登录页面
    jumpToPageLogin () {
      this.$router.replace('/login')
    }
  }
}
</script>
